import React, { useState, useCallback } from 'react';
import Habilities from '../../components/Habilities';
import Projects from '../../components/Projects';
import Parallax from '../../components/Parallax';
import Contact from '../../components/Contact';
import Education from '../../components/Education';

import useWindowDimensions from '../../hooks/useWindowsDimensions';

import { Container, Content, AboutSection } from './styles';

const Dashboard: React.FC = () => {
  const [animated, setAnimated] = useState(false);
  const [mount, setMount] = useState(false);
  const [education, setEducation] = useState(false);
  const { height } = useWindowDimensions();

  const handleScroll = useCallback(() => {
    window.addEventListener('scroll', () => {
      const scrolled = window.scrollY;
      if (scrolled > height/2) {
        setAnimated(true);
      }

      if (scrolled > height) {
        setEducation(true);
      }

      if (scrolled > 1.8*height) {
        setMount(true);
      }
    });
  }, [height]);

  handleScroll();

  return (
    <>
      <Container >
        <Parallax />
      </Container>

      <Content>
        <AboutSection animated={animated}>
          <h3>ABOUT</h3>

          <div>
            <aside>
              <p>
                Hi! I'm a Full-stack programmer formed by Rocketseat.
                I currently work as a freelancer but I am looking for opportunities.
                Hi! I'm a Full-stack programmer formed by Rocketseat.
                I currently work as a freelancer but I am looking for opportunities.
                </p>
            </aside>
          </div>
        </AboutSection>

        <Education education={education} />

        <Habilities mount={mount} />

        <Projects />

        <Contact />

      </Content>
    </>
  );
}

export default Dashboard;
