import styled, { keyframes, css } from 'styled-components';

interface Props {
  education: boolean;
}

export const translate = keyframes`
    from {
        transform: translateX(-200px);
        opacity: 0;
    }

    to {
        transform: translateX(0px);
        opacity: 1;
    }
`;

export const opacityTransform = keyframes`
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
`;

export const Container = styled.div`
  flex-direction: column;
`;

export const EducationContainer = styled.div<Props>`
  display: flex;
  align-items: center;
  flex-direction: row;
  background: #1D1D1D;
  border-radius: 5px;
  width: 100% !important;
  padding: 15px 20px !important;
  margin-top: 20px;
  border: 1px solid #1D1D1D;
  transition: 0.4s all;
  opacity: 0;

  ${props => props.education && css`
    & {
      animation: ${translate} 1s ease-in-out;
      opacity: 1;
    }
  `}

  &:hover {
    border-color: #7159c1;
  }
`; 
export const Content = styled.div`
  display: flex;
  flex-direction: column;
  padding: 0 !important;
  margin-left: 20px;

  span {
    text-align: left;
    color: #666;
    font-size: 10px;
    margin-top: 5px;
  }
`;
export const Title = styled.h3`
  font-size: 14px;
  text-align: left !important;
  font-family: 'Poppins-medium', sans-serif;
  letter-spacing: 1px !important;
`;
export const Description = styled.p`
  margin-top: 5px;
  color: #ccc;
  text-align: left !important;
  font-size: 13px;
  font-family: 'Poppins-medium', sans-serif;

`; 
